<template>
  <div>
    <div class="table">
      <el-button
        class="add_button"
        type="primary"
        size="mini"
        @click="addPerson"
        >新增</el-button
      >
      <table>
        <thead>
          <tr>
            <th>序号</th>
            <th>人员编码</th>
            <th>姓名</th>
            <th>部门</th>
            <th>职位</th>
            <th>入职日期</th>
            <th>手机号码</th>
            <th>具体负责业务</th>
            <th>账号类型</th>
            <th style="width:180px">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in personnelData" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ item.personnel_id }}</td>
            <td>{{ item.user_name }}</td>
            <td>{{ item.department }}</td>
            <td>{{ item.position }}</td>
            <td>{{ item.entry_date }}</td>
            <td>{{ item.phone }}</td>
            <td>{{ item.work }}</td>
            <td>---</td>
            <td style="width:180px">
              <el-button
                type="warning"
                size="mini"
                @click="editPerson(item.personnel_id)"
                >编辑</el-button
              >
              <el-button
                type="danger"
                size="mini"
                @click="removePerson(item.personnel_id)"
                >删除</el-button
              >
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- 编辑用户对话框 -->
    <el-dialog
      title="编辑员工信息"
      :visible.sync="editDialog.editPersonVisible"
      width="50%"
      center
      @close="editDialogClose"
    >
      <!-- 表单内容 -->
      <el-form
        ref="editPersonRef"
        :model="editDialog.editPersonForm"
        label-width="170px"
        :rules="formRules"
      >
        <el-form-item label="人员编码" prop="personnel_id">
          <el-input v-model="editDialog.editPersonForm.personnel_id"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="user_name">
          <el-input v-model="editDialog.editPersonForm.user_name"></el-input>
        </el-form-item>
        <el-form-item label="部门" prop="department">
          <el-input
            v-model="editDialog.editPersonForm.department"
            readonly="readonly"
          ></el-input>
        </el-form-item>
        <el-form-item label="职位" prop="position">
          <!-- <el-input v-model="editDialog.editPersonForm.position"></el-input> -->
          <el-select
            v-model="editDialog.editPersonForm.position"
            placeholder="请选择"
          >
            <el-option
              v-for="item in position"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="editDialog.editPersonForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="入职日期" prop="entry_date">
          <el-date-picker
            v-model="editDialog.editPersonForm.entry_date"
            type="date"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="具体负责业务" prop="work">
          <el-input v-model="editDialog.editPersonForm.work"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialog.editPersonVisible = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="editPersonClick">保 存</el-button>
      </span>
    </el-dialog>

    <!-- 新增用户对话框 -->
    <el-dialog
      title="新增员工"
      :visible.sync="addDialog.addPersonVisible"
      width="30%"
      center
      @close="addDialogClose"
    >
      <!-- 表单内容 -->
      <el-form
        ref="addPersonRef"
        :model="addDialog.addPersonForm"
        label-width="170px"
        :rules="formRules"
      >
        <el-form-item label="人员编码" prop="personnel_id">
          <el-input v-model="addDialog.addPersonForm.personnel_id"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="user_name">
          <el-input v-model="addDialog.addPersonForm.user_name"></el-input>
        </el-form-item>
        <el-form-item label="部门" prop="department">
          <el-input
            v-model="addDialog.addPersonForm.department"
            readonly="readonly"
          ></el-input>
        </el-form-item>
        <el-form-item label="职位" prop="position">
          <!-- <el-input v-model="addDialog.addPersonForm.position"></el-input> -->
          <el-select
            v-model="addDialog.addPersonForm.position"
            placeholder="请选择"
          >
            <el-option
              v-for="item in position"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="addDialog.addPersonForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="入职日期" prop="entry_date">
          <el-date-picker
            v-model="addDialog.addPersonForm.entry_date"
            type="date"
            placeholder="选择日期"
            format="yyyy/MM/dd"
            value-format="yyyy/MM/dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="具体负责业务" prop="work">
          <el-input v-model="addDialog.addPersonForm.work"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialog.addPersonVisible = false">取 消</el-button>
        <el-button type="primary" @click="addPersonConfirm">新 增</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  data() {
    let checkPhone = (rule, value, callback) => {
      const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (regPhone.test(value)) {
        return callback();
      } else {
        callback("请输入正确的手机号码");
      }
    };
    return {
      // 所有员工信息
      personnelData: [],
      // 编辑员工对话框所需数据
      editDialog: {
        editPersonVisible: false,
        editPersonForm: {}
      },
      // 新增员工对话框所需数据
      addDialog: {
        addPersonVisible: false,
        addPersonForm: {
          personnel_id: "",
          user_name: "",
          department: "物流部",
          position: "",
          phone: "",
          entry_date: "",
          work: ""
        }
      },
      //职位
      position: [
        {
          value: "总监",
          label: "总监"
        },
        {
          value: "主管",
          label: "主管"
        },
        {
          value: "专员",
          label: "专员"
        }
      ],
      // 表单验证
      formRules: {
        personnel_id: [
          { required: true, message: "请输入人员编码", trigger: "blur" }
        ],
        user_name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        department: [
          { required: true, message: "请输入部门名称", trigger: "blur" }
        ],
        position: [
          { required: true, message: "请输入职位名称", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ],
        entry_date: [
          { required: true, message: "请输入入职日期", trigger: "change" }
        ],
        work: [{ required: true, message: "请输入具体业务", trigger: "blur" }]
      }
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取人员资料列表
    getPersonList() {
      this.$api2.getPersonList().then(res => {
        // console.log(res);
        this.personnelData = res.data.data;
      });
    },
    // 点击编辑员工
    editPerson(id) {
      console.log(id);
      for (let item of this.personnelData) {
        if (item.personnel_id == id) {
          this.editDialog.editPersonForm = this.$utils.deepClone(item);
        }
      }
      this.editDialog.editPersonVisible = true;
    },
    // 点击新增员工
    addPerson() {
      this.addDialog.addPersonVisible = true;
    },
    // 删除员工
    removePerson(id) {
      this.$confirm("此操作将永久删除该员工, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api2.removePerson(id).then(res => {
            console.log(res.data.data);
            this.getPersonList();
            this.$message({
              type: "success",
              message: "删除成功!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 对话框关闭事件重置表单
    editDialogClose() {
      this.$refs.editPersonRef.resetFields();
    },
    addDialogClose() {
      this.$refs.addPersonRef.resetFields();
    },
    // 确认编辑员工信息
    editPersonClick() {
      this.$refs.editPersonRef.validate(val => {
        if (!val) return;
        let formData = new FormData();
        formData.append(
          "personnel_id",
          this.editDialog.editPersonForm.personnel_id
        );
        formData.append("user_name", this.editDialog.editPersonForm.user_name);
        formData.append(
          "department",
          this.editDialog.editPersonForm.department
        );
        formData.append("position", this.editDialog.editPersonForm.position);
        formData.append("phone", this.editDialog.editPersonForm.phone);
        formData.append(
          "entry_date",
          this.editDialog.editPersonForm.entry_date
        );
        formData.append("work", this.editDialog.editPersonForm.work);

        this.$api2.editPerson(formData).then(res => {
          console.log(res.data);
          this.getPersonList();
          this.$message.success("编辑员工成功!");
          this.editDialog.editPersonVisible = false;
        });
      });
    },
    // 确认添加员工
    addPersonConfirm() {
      this.$refs.addPersonRef.validate(val => {
        if (!val) return false;
        let formData = new FormData();
        formData.append(
          "personnel_id",
          this.addDialog.addPersonForm.personnel_id
        );
        formData.append("user_name", this.addDialog.addPersonForm.user_name);
        formData.append("department", this.addDialog.addPersonForm.department);
        formData.append("position", this.addDialog.addPersonForm.position);
        formData.append("phone", this.addDialog.addPersonForm.phone);
        formData.append("entry_date", this.addDialog.addPersonForm.entry_date);
        formData.append("work", this.addDialog.addPersonForm.work);
        this.$api2.addPersonInfo(formData).then(res => {
          console.log(res.data);
          this.getPersonList();
          this.$message.success("添加员工成功!");
          this.addDialog.addPersonVisible = false;
        });
      });
    }
  },
  created() {
    this.$axios({
      url: this._url2 + "server/stock_management/is_open/",
      method: "get"
    }).then(res => {
      if (res.data.code == 200) {
        this.getPersonList();
      } else {
        alert("你没有权限进入这个页面");
        this.$router.push("/Login");
      }
    });
  },
  mounted() {}
};
</script>
<style scoped>
/* @import url("/static/css/thead_fixed_table.css"); */
@import "../../../static/css/thead_fixed_table.css";
.table {
  width: calc(72vw + 180px);
  margin: auto;
  margin-top: 60px;
}
.add_button {
  margin-left: -92%;
  margin-bottom: 20px;
  width: 100px;
}
.el-date-editor.el-input {
  width: 100%;
}
.el-form > * {
  margin-bottom: 22px;
}
</style>



